.commands {
    background-color: rgba(0, 0, 0, 0.1);
    display         : grid;
    height          : 25vh;
    position        : relative;
}

.find-button {
    display               : block;
    margin                : auto;
    width                 : 50%;
    height                : 70px;
    border                : none;
    /* border-radius      : 5px; */
    border-radius         : 35px;
    color                 : grey;
    box-shadow            : 0px 0px 10px rgba(0, 0, 0, 0.3);
    background-color      : white;
    padding               : 10px;
    outline               : none;
    cursor                : pointer;
    font-size             : 30px;
    user-select           : none;

    display                    : flex;
    justify-content            : center;
    align-items                : center;
    -webkit-tap-highlight-color: transparent;
}

.find-button:active {
    background-color: grey;
    color           : white;
    opacity         : 0.5;
}

.noresults {
    animation: vibrate 0.1s 6 forwards;
}

.notifyNoresults {
    position               : absolute;
    display                : block;
    top                    : 10px;
    height                 : 20px;
    width                  : 80%;
    color                  : grey;
    /* color               : crimson; */
    font-size              : 16px;
    text-align             : center;
    left                   : 50%;
    transform              : translateX(-50%);
    letter-spacing         : 2px;
}


@keyframes vibrate {
    25% {
        /* box-shadow: 0 0 10px 5px rgba(0, 0, 255, 0.3); */
        transform: translateX(-15px);
    }

    75% {
        /* box-shadow: 0 0 10px 5px rgba(0, 0, 255, 0.3); */
        transform: translateX(15px);
    }
}